header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* ===== CTA ===== */
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* ===== HEADER ICONS ===== */

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/* ===== ME ===== */

.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  /* left: 50%;
  transform: translateX(-50%); */
  left: calc(50% - 11rem);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem;
}

.me__header {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50px;
  object-fit: cover;
}

/* ===== SCROLL DOWN ===== */

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/* ====== MEDIA QUERY (MEDIUM DEVICES) ======*/

@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}

/* ====== MEDIA QUERY (SMALL DEVICES) ======*/

@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }
  .header__socials,
  .scroll__down {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  header {
    height: 100vh;
  }
  .scroll__down {
    right: -2.3rem;
    bottom: 13rem;
  }
  .header__socials {
    gap: 0.8rem;
    left: 0;
    bottom: 10rem;
  }
}

@media screen and (max-width: 281px) {
  header {
    height: 100vh;
    padding-top: 1rem;
  }

  .me {
    width: 16rem;
    left: calc(50% - 12rem);
    margin-top: 2rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    position: absolute;
    height: 30rem;
    margin-left: 4rem;
    padding: 2rem 0;
  }
}

@media screen and (max-width: 376px) {
  header {
    height: 100vh;
    padding-top: 2rem;
  }

  .me {
    width: 20rem;
    left: calc(50% - 14rem);
    margin-top: 2rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    position: absolute;
    height: 30rem;
    margin-left: 4rem;
    padding: 2rem 0;
  }
}
